ul.wp-block-ls-blocks-ls-lesson {
  list-style: none;
  max-width: 1000px;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
ul.wp-block-ls-blocks-ls-lesson li {
  display: block;
}
ul.wp-block-ls-blocks-ls-lesson li img {
  width: 100%;
  height: auto;
}
ul.wp-block-ls-blocks-ls-lesson li h5 {
  font-size: 20px;
  margin: 0 0 5px;
  line-height: 1.4;
}